footer {
    display: flex;
    justify-content: space-between;
    width: 80%;
    margin: 10vh auto 5vh;
    .footer_aside {
        width: 33.3333%;
        h2 {
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            text-align: left;
            font-size: 1.85rem;
            font-weight: 500;
            color: rgb(45, 45, 45);
            letter-spacing: 2.5px;
            margin-bottom: 2.5rem;
            line-height: 3rem;
        }
        .footer-menu-container {
            width: 60%;
            display: flex;
            justify-content: space-between;
            .footer-menu-links {
                p {
                    user-select: none;
                    cursor: pointer;
                    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
                    color: rgb(60, 60, 60);
                    margin: 1.75rem 0;
                    font-size: 0.975rem;
                }
            }
        }
        .footer-mail {
            color: rgb(40, 40, 40);
            font-weight: 400;
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            margin: 3.5rem 0;
            font-size: 1.5rem;
        }
        .footer-phone {
            color: rgb(20, 20, 20);
            margin: 3.5rem 0;
            font-size: 1.8rem;
        }
        .footer-icons-container {
            display: flex;
            justify-content: space-between;
            width: 33.333%;
            margin-top: 3.95rem;
            margin-bottom: 6vh;
            .footer-icon {
                cursor: pointer;
                font-size: 1.1rem;
                color: rgb(50, 50, 50);
            }
        }
        .footer-icons-info {
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            color: rgb(30, 30, 30);
            font-size: 1.55rem;
        }
    }
}