.home_page-gallery {
    margin: 0 auto;
    width: 87.5%;
    .shop-special-wrapepr {
        display: flex;
        flex-direction: row;
        justify-content: space-between;
        margin: 17.5vh auto 15vh;
        .shop-special-item {
            text-align: center;
            .icon {
                color: rgb(20, 20, 20);
                font-size: 5rem;
                margin-bottom: 1.75rem;
            }
            .special-item-name {
                font-weight: 400;
                font-size: 1rem;
                letter-spacing: 1.2px;
                word-spacing: 2px;
            }
            .special-item-description {
                font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
                margin-top: 0.25rem;
                color: rgb(175, 175, 175);
                font-size: 0.875rem;
            }
        }
    }
    .home_page-gallery-info {
        color: rgb(25, 25, 25);
        font-weight: 500;
        font-size: 1.15rem;
        user-select: none;
    }
    .home-page-gallery-description {
        display: flex;
        flex-direction: row;
        align-items: center;
        justify-content: space-between;
        h2 {
            margin-top: 2rem;
            line-height: 3rem;
            color: rgb(50, 50, 50);
            font-weight: 500;
            font-size: 2.2rem;
            letter-spacing: 4px;
        }
        button {
            font-size: 0.9rem;
            font-weight: bold;
            margin-right: 7.5rem;
            outline: none;
            background: transparent;
            border: 1px solid black;
            padding: 0.85rem 1.5rem;
            transition: .25s linear;
            cursor: pointer;
            user-select: none;
            &:hover {
                background-color: black;
                color: white;
            }
        }
    }
    .gallery-container {
        margin: 7.5vh auto;
        display: flex;
        width: 100%;
        height: 75vh;
        position: relative;
        .gallery-part {
            display: flex;
            flex-wrap: wrap;
            width: 60%;
            .gallery-image {
                background-position: center;
                width: 50%;
                height: 50%;
                background-size: cover;
            }
        }
        .gallery-part:nth-of-type(2) {
            width: 40%;
            background-position: center;
            background-size: cover;
        }
        .homeStore {
            font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
            position: absolute;
            z-index: 105;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            background-color: rgb(255, 255, 255);
            border-radius: 50%;
            padding: 2rem;
            color: rgb(60, 60, 60);
            height: 8rem;
            width: 8rem;
            display: flex;
            justify-content: center;
            align-items: center;
            font-size: 0.9rem;
            letter-spacing: 0.2px;
            cursor: pointer;
            user-select: none;
        }
    }
}