.home-section {
    margin-top: 5vh;
    height: 100vh;
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: row;
    h1 {
        position: absolute;
        z-index: 10;
        top: 57.5%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 8rem;
        color: rgb(30, 30, 30);
        font-weight: 700;
        letter-spacing: 10px;
        word-spacing: 12px;
    }
    .home-image1 {
        margin-left: 5vw;
        height: 72.5vh;
        width: 27.5vw;
        background-size: contain;
        background-repeat: no-repeat;
        background-position: left;
        position: relative;
        .description {
            width: 100%;
            left: 0;
            bottom: 5%;
            padding: 1.5rem 3rem;
            letter-spacing: 1.5px;
            word-spacing: 3px;
            font-weight: 500;
        }
    }
    .home-image2 {
        height: 90vh;
        width: 50vw;
        background-size: cover;
        background-repeat: no-repeat;
        background-position: center;
        position: relative;
        .description {
            left: 0;
            bottom: 0;
            padding: 2rem 6rem 2rem 1.75rem;
            letter-spacing: 1.5px;
            word-spacing: 3px;
            font-weight: 500;
        }
    }
    aside article {
        background-color: rgb(255, 255, 255);
        position: absolute;
    }
    .description .price {
        margin-top: 0.3rem;
        letter-spacing: 0.5px;
        word-spacing: 0;
        font-weight: normal;
        color: gray;
    }
    .home-decorations {
        display: flex;
        flex-direction: column;
        position: absolute;
        left: 0;
        margin-left: 1.5rem;
        span {
            font-size: 2.75rem;
            font-weight: bold;
            color: rgb(0, 0, 0);
            user-select: none;
        }
    }
    .social-icons-wrapper {
        display: flex;
        flex-direction: column;
        position: absolute;
        right: 0;
        top: 50%;
        transform: translateY(-30%);
        .social-icon {
            margin: 1.2rem 1.25rem;
            color: rgb(0, 0, 0);
            font-size: 2.1rem;
            cursor: pointer;
        }
    }
}